"use client";

import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart, faComment, faPlus } from "@fortawesome/free-solid-svg-icons";
import UploadMenu from "../../Components/UploadMenu";
import "./page.css";
// const images = [
//   { src: "/assets/Img1.jpg" },
//   { src: "/assets/Img2.jpg" },
//   { src: "/assets/Img15.jpg" },
//   { src: "/assets/Img4.jpg" },
//   { src: "/assets/Img5.jpg" },
// ];
export default function Gallery() {
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedPicture, setSelectedPicture] = useState(null);
  const [isPortrait, setIsPortrait] = useState(false); // State to track orientation
  const [loadedImages, setLoadedImages] = useState([]);
  const [addPhotos, setAddPhotos] = useState(false);

  useEffect(() => {
    const fetchImages = async () => {
      try {
        console.log("Fetching images...");
        const response = await fetch("http://localhost:3001/api/images");

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log("Fetched images:", data);
        setLoadedImages(data.data);
      } catch (error) {
        console.error("Error fetching images:", error);
      }
    };

    fetchImages();
  }, []);

  //modal functions
  const openModal = (img: any) => {
    const image = new Image();
    image.src = img.url;

    image.onload = () => {
      setIsPortrait(image.height > image.width); // Check orientation
      setSelectedPicture(img);
      setModalOpen(true);
    };
  };

  const closeModal = () => {
    setModalOpen(false);
    setSelectedPicture(null);
  };

  const [isModalOpen, setIsModalOpen] = useState(false);

  const openUploadMenu = () => {
    setIsModalOpen(true);
  };

  const closeUploadMenu = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="photo-gallery">
      {loadedImages.length > 0 ? (
        loadedImages.map((img: any, index: any) => (
          <div className="photo" key={index}>
            <figure className="hover-effect">
              <img
                src={img.url}
                onClick={() => openModal(img)}
                alt={`Photo ${index + 1}`}
              />
              {/* <div className="gradient-overlay">
              <div className="icon-container">
                <span className="icon">‚ù§Ô∏è</span>
                <span className="icon">üí¨</span>
              </div>
            </div> */}
              <figcaption className="interactions">
                <span>
                  <FontAwesomeIcon icon={faHeart} /> 23
                </span>
                <span>
                  <FontAwesomeIcon icon={faComment} /> 12
                </span>
              </figcaption>
            </figure>
          </div>
        ))
      ) : (
        <p>Loading images...</p>
      )}
      {modalOpen && (
        <div className="modal-overlay" onClick={closeModal}>
          <div
            className={`display-modal ${isPortrait ? "portrait" : "landscape"}`}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Image Section */}
            <img src={selectedPicture?.url} alt="Selected" />

            {/* Interaction Section */}
            <div
              className={`interaction-section ${
                isPortrait ? "portrait" : "landscape"
              }`}
            >
              <h2>{selectedPicture?.title || "Image Title"}</h2>
              <div className="comments">
                <p>Comments:</p>
                <ul>
                  <li>Great shot!</li>
                  <li>Amazing view!</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      )}

      
      <div className="btn-parent">
        <button className="add-btn fas fa-plus" onClick={() => openUploadMenu}>
          <FontAwesomeIcon icon={faPlus} />
        </button>
      </div>
    </div>
  );
}
